import React from "react";

import './CourseRating.css';

export default function CourseRating({ratingPoint, ratedNumber}) {

  function renderRating(ratingPoint) {
    let rating = ratingPoint;
    let ret = [];
    while (rating > 0) {
      if (rating >= 1) {
        ret.push(1);
      } else if (rating >= 0.5) {
        ret.push(0.5);
      } else {
        ret.push(0);
      }
      rating--;
    }
    while (ret.length < 5) {
      ret.push(0);
    }

    return ret.map((value, index) => {
      return value === 1 ? <i key={index} className="fas fa-star rating-icon"/> : (
        value === 0 ? <i key={index} className="far fa-star rating-icon"/> :
          <i key={index} className="fas fa-star-half-alt rating-icon"/>
      )
    });
  }

  return (
    <div className='rating d-flex align-items-center'>
      <div className='rating-point'>{ratingPoint}</div>
      <div className='ml-1 d-flex align-items-center'>
        {renderRating(ratingPoint)}
      </div>
      <div className='rating-number'>({ratedNumber})</div>
    </div>
  );
}
